.navBar {
  /* height: 7rem; */
  padding: 1rem;
  display: flex;
}

.logo {
  width: 8%;
  display: flex;
  justify-content: end;
  align-items: center;
  margin: 0rem 3rem;
}

.logo img {
  height: 3.5rem;
}

.nav1 {
  width: 92%;
  display: flex;
  justify-content: end;
  padding-right: 5rem;
}

.nav_link {
  margin: 0.5rem 2rem;
  color: rgb(172, 171, 171);
  font-size: 1.19rem;
  font-weight: 500;
  text-decoration: none;
}

.navBar a:hover {
  color: white;
  cursor: pointer;
  font-weight: 500;
}

.h {
  height: 32rem;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.home{
  width: 90%;
}

.home1 {
  color: white;
  height: 26rem;
  width: 100%;
  padding: 7rem;
  font-family: League Spartan, Helvetica, Arial, sans-serif;
}

.about {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: #d55f28;
  font-weight: bold;
}

.about:hover {
  background-color: #e76f29;
  font-weight: bolder;
}

.home2 {
  color: white;
  height: 21rem;
  width: 20%;
  padding: 1rem;
  font-family: League Spartan, Helvetica, Arial, sans-serif;
}

.home-icon{
  position: absolute;
  font-size: 17rem;
  color: rgba(99, 116, 247, 0.18);
  top: 9rem;
  left: 35rem;
}

.text1 {
  /* border-right: 10px solid; */
  font-size: 5rem;
  font-weight: bolder;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  margin-bottom: 0%;

  animation:
    typing1 4s steps(20, end) infinite,
    cursor .5s step-end infinite alternate;
}

.text2 {
  /* border-right: 10px solid; */
  font-size: 1.35rem;
  font-weight: bold;
  font-style: italic;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;

  animation:
    typing2 4s steps(40, end) infinite,
    cursor .8s step-end infinite alternate;
}

@keyframes cursor {
  50% {
    border-color: transparent;
  }
}

@keyframes typing1 {
  0% {
    width: 0;
  }

  30% {
    width: 100%;
  }

  70% {
    width: 100%;
  }

  100% {
    width: 0;
  }
}


@keyframes typing2 {
  0% {
    width: 0;
  }

  30% {
    width: 100%;
  }

  70% {
    width: 100%;
  }

  100% {
    width: 0;
  }
}

