.modal-dialog-centered {
  --bs-modal-width: 550px;
}

.modal-content {
  height: 33rem;
  width: 35rem;
}

.m1 {
  padding: 0.5rem;
  background-color: #e4e9e7;
  width: 50%;
}

.modal-title {
  font-weight: bolder;
  font-size: 2rem;
}

.modal-body {
  font-size: 1rem;
}

.modal-body i {
  margin: 0.4rem 1rem;
}

.m2 {
  padding: 0.5rem;
  background-color: #383444;
  width: 50%;
  color: whitesmoke;
}

.modal-header p {
  font-size: 0.9rem;
  font-family: sans-serif;
  font-style: italic;
}

.m1 .modal-header{
  border-bottom: var(--bs-modal-header-border-width) solid #101010;
}

.m2 .modal-header{
  border-bottom: var(--bs-modal-header-border-width) solid var(--bs-modal-header-border-color);
}

.form{
  margin-bottom: 1.08rem;
}



.footer {
  background-color: #000000;
  padding-top: 3rem;
  padding-bottom: 4rem;
  /* padding-left: 5rem; */
