  text-align: center;
  justify-content: center;
}

.f {
  font-family: sans-serif;
  font-size: 1.5rem;
  margin-right: 1.5rem;
  color: whitesmoke;
}

.footer-1 {
  display: flex;
  text-align: center;
  justify-content: center;
  align-self: center;
}

.footer i {
  margin: 0rem 1.2rem;
}

.g {
  background-color: #000000;
  padding: 1rem 2rem;
}

.g1 {
  background-color: #FCCC36;
  height: 0.2rem;
  width: 100%;
  margin-bottom: 0.5rem;
}

.g-p {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 400;
  font-size: 1.1rem;
  font-style: italic;
  color: rgb(169, 204, 223);
}

.g-p-p {
  font-size: 1rem;
  font-style: normal;
}

.g a {
  text-decoration: none;
  color: #FCD13E;
}

.g a:hover {
  color: white;
}

.sidebar {
  align-self: end;
  height: 17rem;
  background-color: #191524;
}

.sidebar i {
  margin: 2rem 1rem;
  color: #cbc7c7;
}

.sidebar i:hover {
  color: #f8f5f5;
}

.p {
  background-color: #e7e5e5;
  padding: 1rem 2rem;
}

.p1 {
  background-color: #0b0b0a;
  height: 0.1rem;
  width: 100%;
  margin-bottom: 0.5rem;
}

.projects-base {
  background-color: #e7e5e5;
}

.projects h5 {
  font-family: sans-serif;
  margin-top: 1rem;
  font-weight: bold;
  color: #c59a80;
}

.projects h1 {
  font-family: sans-serif;
  margin-top: 1rem;
  font-weight: bolder;
  color: #545151;
}


.vdrive img:hover {
  opacity: 25;
}

.project-item {
  margin: 4rem;
  position: relative;
  display: inline-block;
}

.project-image {
  height: 30rem;
  box-shadow: 2rem 2rem 2rem rgba(0, 0, 0, 0.6);
  transition: filter 0.3s ease;
  border-radius: 1rem;
}

.project-item:hover .project-image {
  filter: blur(2px);
}

.overlay1 {
  position: absolute;
  height: 15rem;
  width: 15rem;
  top: 26%;
  left: 40%;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 1;
}

.o1{
  font-size: 5rem;
}

.o2{
  font-size: 2rem;
}

.o1-1{
  font-size: 3rem;
}

.o1-2{
  font-size: 1.35rem;
}

.project-item:hover .overlay1 {
  opacity: 1;
}

